.contacts {
  @include flex($direction: row);
  justify-content: space-between;
  @include indent();
  padding: 10%;
}

.contacts__ivite {
  max-width: 50%;
  color: $dark-color;
  font-family: "Oswald", Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  text-transform: uppercase;
  @include indent();
}

.contacts__address {
  max-width: 45%;
  color: $light-color;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  @include indent();
}

@media screen and (min-width: $tablet-width) {
  .contacts {
    padding: 40px 60px;
  }

  .cat .contacts,
  .fm .contacts {
    background-color: lightblue;
  }

  .contacts p {
    outline: 3px dashed darkslategray;
  }

  .contacts__ivite {
    max-width: 154px;
    text-align: left;
    margin-right: auto;
  }

  .contacts__address {
    max-width: 153px;
    text-align: right;
    margin-left: auto;
  }
}

@media screen and (min-width: $desktop-width) {
  .contacts {
    outline: 3px dashed darkslategray;
    position: absolute;
    width: 570px;
    height: 201px;
    padding: 45px 50px;
    box-sizing: border-box;
    top: 30px;
    left: 110px;
  }
}
