.presentation {
  min-width: $mobile-width;
  width: 100%;
  max-width: 100%;
  min-height: 490px;

  @include flex($direction: column);
  justify-content: flex-start;
  align-items: center;
  @include indent();
  padding: 9.375%;
  text-align: center;

  background-image: url("../../images/mobile/index-background-mobile.jpg");
  background-repeat: no-repeat;
  background-size: 100% auto;
}

@media screen and (min-width: 600px) and (max-width: $tablet-width) {
  .presentation {
    min-height: 1000px;
  }
}

.presentation__title {
  color: #ffffff;
  font-size: 36px;
  font-size: 2.25em;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 2px;

  margin-bottom: 10%;
  @include margin-auto();
}

.presentation__promo-text {
  color: #ffffff;
  /*font-family: "Oswald", Arial, Helvetica, sans-serif;*/
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;
  @include margin-auto();

  margin-bottom: 10%;
}

.presentation__can {
  width: 280px;
  height: 270px;
  @include margin-auto();
  margin-bottom: 10%;
}

.presentation__button {
  width: 100%;
  margin-bottom: 10%;
}

@media screen and (min-width: $tablet-width) {
  .presentation {
    font-family: "Oswald", Arial, Helvetica, sans-serif;
    width: 100%;
    min-height: 800px;
    display: block;
    background-color: #ffffff;
    // background-color: lightgreen;

    text-align: left;
  }

  .presentation__title {
    color: $dark-color;
    font-size: 60px;
    font-weight: 400;
    line-height: 1;

    max-width: 410px;

    margin: 0;
    padding: 0.2em;
    padding-left: 0;
  }

  .presentation__promo-text {
    color: $dark-color;
    font-size: 20px;
    font-weight: 400;
    line-height: 20px;
    text-transform: uppercase;
    margin-bottom: 5%;
  }
}

@media screen and (min-width: $desktop-width) {
  .presentation {
    position: relative;
    min-height: 384px;
    background-color: transparent;
    padding: 0 70px;
    margin-bottom: 70px;
  }

  .presentation__can {
    position: absolute;
    width: 402px;
    height: 349px;
    top: -10%;
    left: 45%;
  }

  .presentation__title {
    max-width: 500px;
    font-size: 60px;
    margin: 0;
    padding: 0.2em;
    padding-left: 0;

    z-index: 10;
  }
}
